import listItem from "../../components/index-list-item/index-list-item";
import { mapGetters, mapState, mapActions } from "vuex";
export default {
    components: {
        listItem
    },
    data() {
        return {
            pageIndex: 0,
            lastPage: false
        };
    },
    computed: {
        ...mapGetters(["isLogin"]),
        ...mapState(["myBlogs"])
    },
    methods: {
        ...mapActions(["getMyBlog"]),
        handleBtn() {
            if (this.isLogin) {
                uni.navigateTo({
                    url: "../editor/editor?type=add"
                });
            } else {
                uni.showToast({
                    title: "请先登录!",
                    duration: 2000,
                    icon: "none"
                });
            }
        }
    },
    onReady() {
        this.getMyBlog();
    },
    async onPullDownRefresh() {
        try {
            this.lastPage = false;
            await this.getMyBlog();
        } catch (error) {
            uni.showToast({
                title: "用户信息无效",
                duration: 2000,
                icon: "none"
            });
        }
        uni.stopPullDownRefresh();
    },
    onReachBottom() {
        this.getMyBlog();
    }
};
